import stringMethods from "../library/stringMethods";

const SingleClientData = ({ fieldsData, client, onInputChange }: any) =>
  fieldsData.map(field => {
    switch (field.fieldType) {
      case "text":
        return (
          <li key={field.fieldName}>
            {new stringMethods(field.fieldName)
              .camelStringToText()
              .firstCharUpperCase()
              .getString()}
            <textarea value={client[field.fieldName]} />
          </li>
        );
      case "number":
        return (
          <li key={field.fieldName}>
            {new stringMethods(field.fieldName)
              .camelStringToText()
              .firstCharUpperCase()
              .getString()}
            <input type="number" value={client[field.fieldName]} onChange={onInputChange}/>
          </li>
        );
      case "select":
        return (
          <li key={field.fieldName}>
            {new stringMethods(field.fieldName)
              .camelStringToText()
              .firstCharUpperCase()
              .getString()}
            <select value={client[field.fieldName]}>
              {field.fieldOptions.map(option => (
                <option key={option.id}>{option.value}</option>
              ))}
            </select>
          </li>
        );
    }
  });

export default SingleClientData;
